import React, { Component, Fragment } from "react";
import {inject, observer} from "mobx-react";

const remote = window.require("electron").remote;
const trackEvent = remote.getGlobal("trackEvent");

const muteIcon = require("../../../assets/images/Mic/big/Muted4.png");

export class Video extends Component {
  constructor(props) {
    super(props);
    this.vidRef = React.createRef();
  }

  componentDidMount() {
    this.updateVideoStream();
  }

  componentDidUpdate() {
    this.updateVideoStream();
  }

  updateVideoStream = () => {
    const { id, callStore } = this.props;

    const stream = callStore.getUserStream(id);

    if (stream === true) {
      return;
    }

    if (stream && this.vidRef && this.vidRef.current && this.vidRef.current.srcObject !== stream) {
      this.vidRef.current.srcObject = stream;
    }
  }

  acceptCall = () => {
    const {callStore} = this.props;
    callStore.acceptCall();
    trackEvent("Call Received", "Answered");
  }

  joinCall = () => {
    const {callStore} = this.props;
    callStore.joinCall();
    trackEvent("Call Received", "Joined")
  }

  declineCall = () => {
    const {callStore} = this.props;
    callStore.rejectCall();
    trackEvent("Call Received", "Declined");
  }

  render() {
    const { participantNo, callStore, workspaceStore, className, id, smallImage, ...rest } = this.props;
    const user = workspaceStore.getUser(id);

    const userCount = callStore.currentPublicWorkspaceId ? workspaceStore.usersInPublicWorkspace(callStore.currentPublicWorkspaceId).length - 1 : callStore.userCount;

    const isPreview = (user) => {
      return user && callStore.knocking.inbound && (callStore.beingKnocked.caller.id === user.id);
    };

    const positionClassName = {
      1: "CallVideo-Participant Top-Left-Participant",
      2: "CallVideo-Participant Top-Center-Participant",
      3: "CallVideo-Participant Top-Right-Participant",
      4: "CallVideo-Participant Bottom-Left-Participant",
      5: "CallVideo-Participant Bottom-Right-Participant",
    }

    let style = userCount > 3 ? {width: 279, height: 279} : {};
    style = isPreview(user) ? {
      ...style,
      filter: "grayscale(100%)",
      boxShadow: "-2px 2px 6px #333"
    } : style;

    let positionStyle = (participantNo === 5 && isPreview(user)) ? {
      right: 123
    } : {};

    return (
      <div 
        className={(userCount < 4 || !positionClassName[participantNo]) ? 'CallVideo-Participant' : positionClassName[participantNo]}
        style={
          participantNo >= 4 ? {
            display: 'flex',
            alignItems: 'center',
            ...positionStyle
          } : (!smallImage ? {
            paddingTop: 23,
            paddingLeft: 11
          } : {})
        }
      >
        <div
          style={
            isPreview(user) ? {
              display: "flex",
              alignItems: "center",
              position: "relative",
            } : {
              position: "relative",
            }
          }
        >
          <video 
            className={ className || "call-screen-user-video" }
            controls={false}
            ref={this.vidRef}
            autoPlay={true}
            style={style}
            data-forcerender={`${callStore.streamCounter}`}
            {...rest}
          />
          { isPreview(user) && <div className="knocking-label">Knocking</div> }
          { isPreview(user) && <div className="knocking-shorten-name">{callStore.beingKnocked.caller.short_name}</div> }
          { user && user.status && user.status.micMuted && (
            <div
              className="CallVideo-Participant-microphone"
              style={{
                bottom: (userCount <= 3 || participantNo >= 4) ? "30px" : "66px",
                right: (userCount <= 3 || participantNo >= 4) ? "0px" : "-11px",
              }}
            >
              <img
                style={{ width: "25px", height: "37px"}}
                alt="mic"
                src={muteIcon}
              />
              <p>Muted</p>
            </div>
          )}
        </div>
        {
          isPreview(user) && (
            <Fragment>
              <div 
                className="knockee-btn-container"
                style={
                  participantNo >= 4 ? {
                    float: "right",
                    display: "flex",
                    flexDirection: "column-reverse",
                    marginLeft: 15
                  } : {}
                }
              >
                <button
                  className="btn-declin"
                  onClick={this.declineCall}
                >
                  Decline
                </button>
                <button
                  className="btn-answer"
                  onClick={(callStore.callId && callStore.userCount > 1) ? this.joinCall : this.acceptCall}
                  style={
                    participantNo >= 4 ? {
                      marginBottom: 15
                    } : {}
                  }
                >
                  {(callStore.callId && callStore.userCount > 1) ? "Add to Call" : "Answer"}
                </button>
              </div>
            </Fragment>
          )
        }
    </div>
    );
  }
}

export default inject('callStore', 'workspaceStore')(observer(Video));
