import React from 'react';
import {inject, observer} from 'mobx-react';
import UserThumbnail from "./UserThumbnail";
import { Fragment } from 'react';

const PublicWorkspace = ({ workspaceStore, myData, publicWorkspaceId, joinToWorkSpace }) => {
  const thumbs = [];

  const usersInPublicWorkspace = workspaceStore.usersInPublicWorkspace(publicWorkspaceId);

  for (let i=0; i<usersInPublicWorkspace.length; i++) {
    // this is a tuple in format [id, user]
    const id = usersInPublicWorkspace[i];

    if (id !== myData.userId) {
      thumbs.push(
        <UserThumbnail key={id} id={id} clickable={false}/>
      );
    }
  }

  return (
      <Fragment>
          {thumbs}
          <div
            className="user-video add-to-public-workspace-btn"
            readonly
            onDrop={() => {joinToWorkSpace(publicWorkspaceId);}}
            onDragOver={(ev) => {ev.preventDefault();}}
            onClick={() => {joinToWorkSpace(publicWorkspaceId);}}
          >
            <button
              style={{ cursor: 'default' }}
            >Join</button>
          </div>
      </Fragment>
  );
};

export default inject('myData', 'workspaceStore')(observer(PublicWorkspace));