import React, { Component } from "react";
import {inject, observer} from "mobx-react";
import CallControls from "./CallControls";
import CallVideo from "./CallVideo";
const closeBtn = require("../../../assets/images/close-btn.png");

export class CallScreen extends Component {
  render() {
    const { myData, callStore, workspaceStore, dash } = this.props;
    const currentPublicWorkspaceId = callStore?.currentPublicWorkspaceId;
    return (
      <div className="main-video-container" style={{ marginLeft: "5px", }}>
        {
          !callStore.knocking.inbound && <CallControls dash={dash} returnStream={callStore.startScreenShare} />
        }

        {
          !currentPublicWorkspaceId
            && callStore.peopleInCall.map((id, index) => 
              <CallVideo participantNo={index + 1} key={id} id={id} smallImage={false}/>
            )
        }
        {
          currentPublicWorkspaceId
            && workspaceStore.usersInPublicWorkspace(currentPublicWorkspaceId).map((id, index) => 
              id !== myData.userId && <CallVideo participantNo={index + 1} key={id} id={id} smallImage={false}/>
            )
        }

        <div className="new-user-video"></div>

        <button onClick={callStore.callId ? callStore.hangupCall : callStore.leaveSpace} className="btn-close">
          <img alt="Close" src={closeBtn} style={{ width: "48px" }} />
        </button>
      </div>
    );
  }
}

export default inject('myData', 'callStore', 'workspaceStore')(observer(CallScreen));